import React from "react";
import "./explore.css";
import styled from "styled-components";

export const Explore = () => {
  const Card = (props) => {
    const Card1 = styled.div`
      ${props.type === "math" ? "background: #dd4444;" : ""}
      ${props.type === "chemistry" ? "background: var(--blue-bantuin)" : ""}
    `;
    return (
      <Card1 className="card-in">
        <p style={{ opacity: "0.8", color: "var(--light)" }}>{props.type}</p>
        <h3 style={{ color: "var(--react-light)" }}>Username</h3>
        <p style={{ marginTop: "1em", fontSize: ".8em" }}>
          Here is where question meant to be written{" "}
        </p>
      </Card1>
    );
  };
  return (
    <header className="App-header-main">
      <div className="main-card">
        <h1>Last Minute Problem</h1>
        <div className="bungkus">
          <Card type="math" />
          <Card type="chemistry" />
        </div>
        <a href="/">&rarr; See All</a>
      </div>
    </header>
  );
};
