import React from "react";
import { ReactImg } from "../App";
import { SignOutGoogle } from "..";
import { HashRouter as Router, Link } from "react-router-dom";

class AboutProfile extends React.Component {
  render() {
    return (
      <div id="profile-log-out">
        {console.log(this.props.username)}
        <button className="logout-button" onClick={SignOutGoogle}>
          Keluar
        </button>
      </div>
    );
  }
}

export const About = (props) => {
  return (
    <header className="App-header">
      <ReactImg />
      <h1>My Profile</h1>
      <div style={{ margin: "1em 0 0 0" }}>
        <p style={{ color: "var(--react-dark)", fontSize: ".75em" }}>Name : </p>
        <b>{props.username || "-"}</b>
      </div>
      <div style={{ margin: "1em 0" }}>
        <p style={{ color: "var(--react-dark)", fontSize: ".75em" }}>
          Email :{" "}
        </p>
        <b>{props.email || "-@gmail.com"}</b>
      </div>
      <p>
        {props.username != null ? (
          <Router>
            <Link to="/">
              <AboutProfile username={props.username} />
            </Link>
          </Router>
        ) : null}
      </p>
    </header>
  );
};
